import { ComponentProps, FC, useMemo } from 'react';
import classNames from 'classnames';

// Styles
import './GridArea.scss';

type GridTrackSpan =
  | number // track span
  | {
      start: number;
      span: number;
    };

type GridAreaSpan =
  | GridTrackSpan // column
  | {
      col: GridTrackSpan;
      row: GridTrackSpan;
    };

export interface GridAreaProps extends ComponentProps<'div'> {
  slim: GridAreaSpan;
  small: GridAreaSpan;
  medium?: GridAreaSpan; // Will fallback to `small` if not defined
  large?: GridAreaSpan; // Will fallback to `medium` if not defined
  wide?: GridAreaSpan; // Will fallback to `large` if not defined
}

const isSpanEntryDefined = (
  spanEntry: [string, GridAreaSpan | undefined],
): spanEntry is [string, GridAreaSpan] => {
  const [, span] = spanEntry;
  return span !== undefined;
};

const isGridTrackSpan = (span: GridAreaSpan): span is GridTrackSpan =>
  typeof span === 'number' || ('start' in span && 'span' in span);

const getTokensFromGridTrackSpan = (
  reflowpoint: string,
  axis: 'col' | 'row',
  span: GridTrackSpan,
): Record<string, number> => {
  if (typeof span === 'number') {
    return { [`--sb-grid-area-${reflowpoint}-${axis}-span`]: span };
  }

  return {
    [`--sb-grid-area-${reflowpoint}-${axis}-span`]: span.span,
    [`--sb-grid-area-${reflowpoint}-${axis}-start`]: span.start,
  };
};

const getTokensFromGridAreaSpan = (
  reflowpoint: string,
  span: GridAreaSpan,
): Record<string, number> => {
  if (isGridTrackSpan(span)) {
    return getTokensFromGridTrackSpan(reflowpoint, 'col', span);
  }

  return {
    ...getTokensFromGridTrackSpan(reflowpoint, 'col', span.col),
    ...getTokensFromGridTrackSpan(reflowpoint, 'row', span.row),
  };
};

const GridArea: FC<GridAreaProps> = ({
  slim,
  small,
  medium,
  large,
  wide,
  className,
  style,
  ...other
}) => {
  const tokens = useMemo(
    () =>
      Object.entries({ slim, small, medium, large, wide })
        .filter(isSpanEntryDefined)
        .reduce<Record<string, number>>(
          (tokens, [reflowpoint, span]) => ({
            ...tokens,
            ...getTokensFromGridAreaSpan(reflowpoint, span),
          }),
          {},
        ),
    [slim, small, medium, large, wide],
  );

  return (
    <div
      className={classNames('sb-grid-area', className)}
      style={{ ...tokens, ...style }}
      {...other}
    />
  );
};

export default GridArea;
